import {and, or, draw, node} from './tools/circuits'
import QGen from './QGen'


function randomCircuit(vars, {simplifyRate=.5}={}) {
  const rd = this.random
  const l = vars.length
  if (l === 1) {
    return node(vars[0])
  }
  if (l === 2) {
    if (rd.bool()) {
      return and(vars[0], vars[1])
    } else {
      return or(vars[0], vars[1])
    }
  }
  const i = rd.randint(1,l-1)
  const left = randomCircuit.call(this, vars.slice(0,i))
  const right = randomCircuit.call(this, vars.slice(i))
  if (rd.bool()) {
    return and(
      rd.random() < simplifyRate ? left.simplify({recursive: false}) : left, 
      rd.random() < simplifyRate ? right.simplify({recursive: false}) : right,
    )
  } else {
    return or(
      rd.random() < simplifyRate ? left.simplify({recursive: false}) : left, 
      rd.random() < simplifyRate ? right.simplify({recursive: false}) : right,
    )
  }
}

export class WriteCircuit extends QGen {
  static info = {
    name: 'Write Circuit',
    description: 'Write the formula for a circuit'
  }

  static options = {
    singleColumn: true,
  }

  static params = {
    numberOfNodes: {
      type: 'multiple-select',
      minSelections: 1,
      label: 'Number of variables',
      options: [4, 5, 6, 7, 8, 9, 10, 11]
    }
  }

  static defaultParams = {
    numberOfNodes: {
      4: false, 5: true, 6: false, 7: false, 
      8: false, 9: false, 10: false, 11: false
    }
  }

  generate(params) {
    const numberOfNodes = +this.random.choice(Object.keys(params.numberOfNodes).filter(k => params.numberOfNodes[k]))
    let letters = [
      ...(numberOfNodes > 11
        ? 'abcdefghijklmnopqrstuvwxyz'
        : this.random.choice(['abcdefghijk', 'pqrstuvwxyz'])
      ).slice(0,numberOfNodes)
    ]
    if (this.random.bool()) {
      letters = letters.reverse()
    }
    const circuit = randomCircuit.call(this, letters, {simplifyRate: .5})
    
    const answer = circuit.simplify().toLatex()
    const w = circuit.requiredSpace().width / 8
    const options = w > 2.5 ? {singleColumn: false} : {}
    return {
      instructions: 'Write a logical expression to represent the circuit.',
      answer: '$$' + answer + '$$',
      diagram: draw(circuit, {width: w + 'in'}),
      options,
    }
  }
}

export class DrawCircuit extends QGen {
  static info = {
    name: 'Draw Circuit',
    description: 'Draw the circuit for a logic formula'
  }

  static options = {
    singleColumn: true,
  }

  static params = {
    numberOfNodes: {
      type: 'multiple-select',
      minSelections: 1,
      label: 'Number of variables',
      options: [4, 5, 6, 7, 8, 9, 10, 11]
    }
  }

  static defaultParams = {
    numberOfNodes: {
      4: false, 5: true, 6: false, 7: false, 
      8: false, 9: false, 10: false, 11: false
    }
  }

  generate(params) {
    const numberOfNodes = +this.random.choice(Object.keys(params.numberOfNodes).filter(k => params.numberOfNodes[k]))
    let letters = [
      ...(numberOfNodes > 11
        ? 'abcdefghijklmnopqrstuvwxyz'
        : this.random.choice(['abcdefghijk', 'pqrstuvwxyz'])
      ).slice(0,numberOfNodes)
    ]
    if (this.random.bool()) {
      letters = letters.reverse()
    }
    const circuit = randomCircuit.call(this, letters).simplify()
    const w = circuit.requiredSpace().width / 10
    //const options = numberOfNodes > 7 ? {singleColumn: false} : {}
    return {
      instructions: 'Sketch a circuit to represent the logical expression.',
      question: '$$' + circuit.toLatex() + '$$',
      answer: {
        prompt: ['EmptySpace', {height: '1.5in', width: '1.5in'}],
        correct: draw(circuit, {width: w + 'in'}),
      },
    }
  }
}
