const VGAP = 1
const HGAP = 1
const NODERADIUS = 1

function drawCircuit(circuit, opts={}) {
  const {width, above, below} = circuit.requiredSpace()
  return [
    'CoordinatePlane', 
    {
      span: [-HGAP, -below-VGAP, +width+HGAP, +above+VGAP], 
      axis: false, 
      grid: false, 
      style: 'circuit',
      ...opts,
    },
    ['Point', {x: 0, y: 0}],
    ...circuit.draw({start: {x:0,y:0}}),
    ['Point', {x: width, y: 0}]
  ]
}

class Node {
  constructor(label) {
    this.label = label
  }

  requiredSpace() {
    return {
      width: 2*NODERADIUS + 2*HGAP, 
      height: 2*NODERADIUS + 2*VGAP, 
      above: NODERADIUS + VGAP, 
      below: NODERADIUS + HGAP,
    }
  }

  toLatex() {
    return this.label
  }

  simplify() {
    return new Node(this.label)
  }

  draw({start={x:0, y:0}, availableWidth}={}) {
    const {x, y} = start
    const extra = availableWidth ? (availableWidth - this.requiredSpace().width)/2 : 0
    const cx = x + HGAP + NODERADIUS + extra
    return [
      ['Path', {points: `${x},${y} ${cx-NODERADIUS},${y}`}],
      ['Circle', {cx, cy: y, r: NODERADIUS}],
      ['Path', {points: `${cx+NODERADIUS},${y} ${x+2*HGAP+2*NODERADIUS+2*extra},${y}`}],
      ['Overlay', {x: cx, y: .1+y}, '$$' + this.label + '$$'],
    ]
  }
}

class CircuitOperation {
  constructor(...args) {
    this.args = args.map(a => typeof a === 'string' ? new Node(a) : a)
  }

  argsToLatex() {
    return this.args.map(a => {
      if (a instanceof CircuitOperation) {
        return '\\left(' + a.toLatex() + '\\right)'
      } else {
        return a.toLatex()
      }
    })
  }
}

class And extends CircuitOperation {

  toLatex() {
    return this.argsToLatex().join(' \\land ')
  }

  simplify({recursive=true}={}) {
    let args = []
    for (const a of this.args) {
      const s = recursive ? a.simplify({recursive}) : a
      if (s instanceof And) {
        args = [...args, ...s.args]
      } else {
        args = [...args, s]
      }
    }
    return new And(...args)
  }

  requiredSpace() {
    let w = 0, a = 0, b = 0, h = 0
    for (const x of this.args) {
      const req = x.requiredSpace()
      w += req.width
      if (req.height > h) {
        h = req.height
      }
      if (req.above > a) {
        a = req.above
      }
      if (req.below > b) {
        b = req.below
      }
    }
    return {width: w, height: h, above: a, below: b}
  }

  draw({start={x:0, y:0}, availableWidth}={}) {
    let {x, y} = start
    const extra = availableWidth ? availableWidth - this.requiredSpace().width : 0
    const between = extra / (this.args.length+1)

    const connections = [
      ['Path', {points: [[x, y], [x+between, y]]}]
    ]
    x += between

    let parts = []
    for (const a of this.args) {
      parts = parts.concat(a.draw({start: {x, y}}))
      x += a.requiredSpace().width
      connections.push(['Path', {points: [[x,y], [x+between, y]]}])
      x += between
    }

    return [...connections, ...parts]
  }
}

class Or extends CircuitOperation {

  toLatex() {
    return this.argsToLatex().join(' \\lor ')
  }

  simplify({recursive=true}={}) {
    let args = []
    for (const a of this.args) {
      const s = recursive ? a.simplify({recursive}) : a
      if (s instanceof Or) {
        args = [...args, ...s.args]
      } else {
        args = [...args, s]
      }
    }
    return new Or(...args)
  }

  requiredSpace() {
    let w = 0, h = 0
    for (const a of this.args) {
      const req = a.requiredSpace()
      if (req.width > w) {
        w = req.width
      }
      h += req.height
    }
    w += 2*HGAP
    //h += (this.args.length - 1)*VGAP
    return {width: w, height: h, above: h/2, below: h/2}
  }

  draw({start={x: 0, y: 0}, availableWidth}={}) {
    let {x, y} = start
    const requiredSpace = this.requiredSpace()
    const width = availableWidth ? availableWidth : requiredSpace.width
    const extraW = HGAP + (width - requiredSpace.width)/2
    const lines = [
      ['Path', {points: `${x},${y} ${x+extraW},${y}`}],
      ['Path', {points: `${x+width-extraW},${y} ${x+width},${y}`}]
    ]
    x += extraW

    const ys = []
    y += requiredSpace.above
    for (const a of this.args) {
      const req = a.requiredSpace()
      y -= req.above
      ys.push(y)
      y -= req.below
      //y -= VGAP
    }
    lines.push(['Path', {
        points: [
          [x, ys[0]], 
          [x, ys[ys.length-1]],
        ],
    }])
    let parts = []
    for (let i = 0; i < this.args.length; i++) {
      parts = parts.concat(this.args[i].draw({
        start: {x, y: ys[i]}, 
        availableWidth: width - 2*extraW
      }))
    }
    x += width - 2*extraW
    lines.push(['Path', {
      points: [
        [x, ys[0]], 
        [x, ys[ys.length-1]],
      ],
    }])

    return [...lines, ...parts]
  }
}

const node = (...args) => new Node(...args)
const and = (...args) => new And(...args)
const or = (...args) => new Or(...args)
const draw = drawCircuit

export {
  node,
  and, 
  or, 
  draw
}