import React from 'react'
import Page from '../Page'
import { connect } from 'react-redux'
import { renderObject, RenderComponent } from 'rendering'
import Text from 'rendering/renderTypes/Text'
import {
  CoordinatePlane, Label, Path, Style, Grid
} from 'rendering/renderTypes/VectorGraphics'
import { FontTest, FontDemo } from './FontTest'
import { node, and, or, draw } from 'questions/tools/circuits'
import styles from './scratch.module.css'
import { get, round } from 'constants/lodash'



const vegagraph = () => {
  const domain = [-10,20]
  const range = [-10,10]

  const points = []
  const f = x => 6*Math.sin(x)
  const g = x => 9*Math.cos(x/2)
  let t = domain[0]
  const step = .2
  while (t <= domain[1]) {
    points.push({x: round(t,1), y: f(t), f: 'f'})
    points.push({x: round(t,1), y: g(t), f: 'g'})
    t += step
  }

  return ['Vega', {
    "$schema": "https://vega.github.io/schema/vega/v4.json",
    "width": 300,
    "height": 200,
    "padding": 10,
  
    "data": [
      {
        name: "table",
        values: points
      }
    ],
  
    scales: [
      {
        name: "x",
        type: "linear",
        range: "width",
        domain: domain,
        zero: true,
      },
      {
        name: "y",
        type: "linear",
        range: "height",
        domain: range,
        zero: true,
      },
      {
        name: "color",
        type: "ordinal",
        domain: {"data": "table", "field": "f"},
        range: ['var(--vg-function-stroke-1)', 'var(--vg-function-stroke-2)'],
      },
    ],
  
    axes: [
      {
        scale: "x",
        orient: "bottom", 
        title: 'x-axis',
        grid: true,
        ticks: false,
        tickMinStep: 1,
        tickCount: domain[1] - domain[0] + 1,
        labelOverlap: 'parity',
        labelPadding: 5,
        labelSeparation: 50,
        domain: false,
      },
      {
        scale: "y",
        orient: "left", 
        grid: true,
        ticks: false,
        tickMinStep: 1,
        tickCount: range[1] - range[0] + 1,
        labelOverlap: 'parity',
        labelPadding: 5,
        domain: false,
      },
      {
        scale: 'x',
        orient: 'bottom',
        labels: false,
        ticks: false,
        grid: true,
        //tickCount: 1,
        tickMinStep: 800,
        gridColor: 'black',
        domain: false,
      },
      {
        scale: 'y',
        orient: 'left',
        labels: false,
        ticks: false,
        grid: true,
        tickCount: 1,
        gridColor: 'black',
        domain: false,
      },

    ],
  
    marks: [
      {
        type: "group",
        from: {
          facet: {
            name: "series",
            data: "table",
            groupby: "f"
          }
        },
        marks: [
          {
            type: "line",
            from: {"data": "series"},
            encode: {
              enter: {
                x: {scale: "x", field: "x"},
                y: {scale: "y", field: "y"},
                stroke: {
                  scale: "color", 
                  field: "f"
                },
                strokeWidth: {value: 'var(--vg-function-stroke-width)'},
                strokeOpacity: {value: 1},
              },
              /*
              "update": {
                "interpolate": {"signal": "interpolate"},
                "fillOpacity": {"value": 1}
              },
              */
              hover: {
                strokeWidth: {value: 3}
              },
              update: {
                strokeWidth: {value: 'var(--vg-function-stroke-width)'}
              }
            }
          }
        ]
      }
    ]
  }]
}





const data = vegagraph()



const Scratch = () => {
  const torender = renderObject(data)
  
  return (
    <Page>
      <div className="page-content">
        {torender}
      </div>
    </Page>
  )
}

export default Scratch